import { observable, runInAction, action } from 'mobx'
import auth from '../utils/auth'

export default new class User {
  @observable
  id = undefined

  @observable
  displayName = undefined

  @observable
  image = undefined

  @observable
  options = {
    preference: undefined
  }

  constructor () {
    auth().then(({ id, displayName, image, options = {} } = {}) => {
      if (!id) return
      runInAction(() => {
        Object.assign(this, { id, displayName, image, options })
      })
    })
  }

  @action
  changeOptionsPreference = preference => {
    this.options.preference = preference
  }
}



